import React from "react";
import App, { Container } from "next/app";

class AppLayout extends App {
  static async getInitialProps({ Main, ctx }) {
    let pageProps = {};

    if (Main.getInitialProps) {
      pageProps = await Main.getInitialProps(ctx);
    }

    return { pageProps };
  }

  render() {
    const { Main, pageProps } = this.props;

    return (
      <Container>
        <h1>I'm everywhere</h1>
        <Main {...pageProps} />
      </Container>
    );
  }
}

export default AppLayout;
